#app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
}

#app-container .input-fields {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#app-container .input-fields input {
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: #fff;
  width: 300px;
  text-align: center;
}

#app-container button {
  padding: 10px;
  margin: 0px;
  color: #fff;
  font-weight: bolder;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: #85c99d;
  cursor: pointer;
  width: 322px;
  margin-bottom: 10px;
}

#html-to-png {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
}

#html-to-png h2 {
  font-weight: 500;
  padding: 0px 0px 0px 0px;
  margin: 0px 0px 5px 0px;
  max-width: 300px;
  text-align: center;
}

#html-to-png h4 {
  font-weight: 400;
  padding: 0px 0px 0px 0px;
  margin: 0px 0px 20px 0px;
}

#html-to-png p {
  padding: 0px 0px 0px 0px;
  margin: 20px 0px 0px 0px;
}
